@import "./../../styles";

.carusel {
  
  max-width: 1820px;
  margin: 0px auto;
  position: relative;
  z-index: 1;
  
  @media (max-width: 767px) {
    margin: 0px -20px;
  }
  &__slider {
    position: relative;
    
  }
  &__arrow {
    position: absolute;
    top: calc(50% - 40px);
    z-index: 10;
    @media (max-width: 767px) {
      display: none !important;
    }
    &_left {
      left: 2%;
    }
    &_right {
      right: 2%;
    }
  }
  &__pugination {
    width: 100%;
    display: flex;
    justify-content: center;
    column-gap: 28px;
    position: absolute;
    bottom: 43px;
    left: 0;
    z-index: 10;
    @media (max-width: 768px) {
      column-gap: 16px;
    }
    & .swiperPag {
      background: transparent;
      height: 8px;
      width: 8px;
      border-radius: 50%;
      border: 2px solid #ffffff;
    }
    & .swiperPagActive {
      background: #ffffff;
    }
  }
}





.slide {
  width: 100%;
  height: 700px;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  z-index: 30;
  @media (max-width: 767px) {
    height: 650px;
    border-radius: 0px;
  }
  &__content {
    box-sizing: border-box;
    padding: 0px 100px;
    width: 100%;
    height: 100%;
    @include container;
    position: relative;
    z-index: 30;
    display: flex;
    flex-direction: column;
    justify-content: center;
    //align-items: center;
    @media (max-width: 767px) {
      padding: 0px 20px;
      align-items: center;
      text-align: center;
    }
  }
  &__title {
    max-width: 570px;
    font-size: 56px;
    font-weight: 700;
    line-height: 126%;
    text-transform: uppercase;
    margin: 0px 0px 8px 0px;
    @media (max-width: 767px) {
      font-size: 32px;
      
    }
  }
  &__text {
    max-width: 570px;
    font-size: 20px;
    font-weight: 400;
    line-height: 135.5%;
    margin: 0px 0px 37px 0px;
    @media (max-width: 768px) {
      font-size: 16px;
    }
  }
  &__bgImage {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__bgGradient {
    background: linear-gradient(95deg, rgba(4, 5, 17, 0.72) 1.89%, rgba(88, 88, 88, 0.00) 65.32%), rgba(211, 211, 211, 0) -9.324px -7.906px / 145.466% 159.558% no-repeat;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 20;
  }
}