html {
	//overflow: hidden;
}
* {
	box-sizing: border-box;
}


body {
  background: #282B38;
  color: #FFF;
	padding: 0 50px;
	height: 100%;
	margin: 0px auto;
	overflow-x: hidden;
	//overflow: scroll;
	@media (max-width: 1024px) {
		padding: 0 30px;
	}
	@media (max-width: 768px) {
		padding: 0 20px;
	}
}

a {
  color: #FFF;
  text-decoration: none;
}

li {
	list-style-position: inside;
}






